#facts {
  background: $green;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;

  &__wrapper {
    display: flex;
  }
}

.fact {
  padding: $fact_padding-y 0;
  // same width for all facts
  flex: 1;
  border-left: $fact_border;

  &:last-child {
    border-right: $fact_border;
  }

  &__number,
  &__text {
    margin: 0;
  }

  &__number {
    margin-bottom: $fact__number_margin-bottom;

    font-size: $fact__number_font-size;

    @include media-breakpoint-down(md) {
      font-size: $fact__number_font-size * 0.7;
    }

    @include media-breakpoint-down(xs) {
      font-size: $fact__number_font-size * 0.5;
    }

    font-weight: 700;
  }

  &__text {
    font-size: $fact__text_font-size;

    @include media-breakpoint-down(lg) {
      font-size: $fact__text_font-size * 0.8;
    }
  }
}
